.top-two-thirds {
	height: 60vh;
}

.bottom-third {
	height: 30vh;
}

canvas {
	background-color: #d3d3d3;
	width: 100%;
	border-radius: 10px;
}

.form-header-print {
	display: none;
}

#top-cards {
	gap: 1%;
}

#contract-card {
	min-width: 97.5vw !important;
	min-height: 95vh;
}

#customer-auth-card {
	min-height: 100%;
	max-height: 100%;
	min-width: 100%;
	max-width: 100%;
}

/*Contract Form Card Sizing*/
#terms-card {
	min-height: 100%;
	max-height: 100%;
	min-width: 60%;
	max-width: 60%;
}

#membership-options-card {
	min-height: 100%;
	max-height: 100%;
	min-width: 39%;
	max-width: 39%;
}

#customer-auth-card-card-content {
	display: grid;
	grid-template-columns: 70% 30%;
}

#auth-form-cont {
	width: 100%;
	display: grid;
	height: 100%;
	grid-template-columns: repeat(3, 33.3%);
}
#auth-form-cont>.form-info-item {
	display: grid;
	grid-template-columns: 25% 60%;
	align-items: center;
	justify-items: right;
	margin: 5px;
	gap: 10%;
}
#auth-form-cont>.form-info-item>input {
	width: 100%;
	height: 40px;
}
#auth-form-cont>.form-info-item>.form-info-title {
	font-size: 22.5px;
	width: 100%;
}

#auth-submit-cont {
	width: 100%;
}

#verification-text {
	text-align: left;
	font-size: smaller;
	font-style: italic;
	color: #212121;
	font-weight: 550;
}

#clear-canvas {
	width: 150px;
}

#submit-form {
	background-color: rgb(24, 93, 116);
}
#submit-form:hover {
	background-color: rgb(13, 68, 87);
}
#print-form {
	background-color: rgb(24, 116, 44);
}
#print-form:hover {
	background-color: rgb(14, 88, 30);
}

#form-actions {
	padding: 10px;
}